.skills-card{
    border-radius: 0.65rem;
    border: 1.5px solid #6bb7d4;
    padding: 2.5rem;
    text-align: center;
    background: #080b0c;
    backdrop-filter: blur(1rem);
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease;
}

.skills-card:hover,
.skills-card:active{
    background: linear-gradient(180deg, #c0bdce 0%, #3f5f49 100%);
}

.skills-card span{
    font-size: 1.3rem;
    font-weight: 500;
}

.skill-icon{
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #080b0c;
    border-radius: 0.65rem;
    border: 1.5px solid#6bb7d4;
    position: absolute;
    top: -1rem;
    left: -1rem;
}

.skill-icon img{
    width: 2.4rem;
    height: auto;
    object-fit: contain;
}

@media (max-width: 1025px){
    .skills-card{
        padding: 2rem;
    }

    .skills-card span{
        font-size: 1rem;
    }

    .skill-icon{
        width: 3rem;
        height: 3rem;
    }

    .skill-icon img{
        width: 1.8rem;
    }
}

@media (max-width: 769px){
    .skills-card span{
        font-size: 0.9rem;
    }
}